import { defineComponent } from "vue";

export default defineComponent({
  name: "MenuBlockComponent",
  props: {
    id: Number,
    blockId: String,
    data: Object,
  },
  computed: {
    blockTypeClass() {
      let c = "";
      switch (this.data!.block_type) {
        case "ACT":
          c = "bg-primary";
          break;
        case "TRG":
          c = "bg-secondary";
          break;
        case "LOG":
          c = "bg-info";
          break;
        default:
      }
      return c;
    },
  },
  methods: {
    drag(ev: any) {
      ev.dataTransfer.setData("node", ev.target.getAttribute("data-node"));
      ev.dataTransfer.setData("data", JSON.stringify(this.data));
    },
  },
});
